import React, { useContext, useEffect, useState } from "react";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import IconButton from "@material-ui/core/IconButton";
import TextField from "@material-ui/core/TextField";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";

import fs from "fs";
import axios from "axios";

const ListView = () => {
  const srcs = ["https://www.amazon.com/"];
  const [src, setSrc] = useState("");
  const [srcInput, setSrcInput] = useState(src);
  const [items, setItems] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [img, setImg] = useState();
  const [title, setTitle] = useState();
  const reader = new FileReader();

  // Adjust to both view and edit

  const renderListItems = () => {
    return items.map((item) => (
      <ListItem
        button
        key={item._id}
        onClick={(e) => {
          if (e.target.classList.contains("btn")) setSrc(item.link);
        }}
      >
        <ListItemText primary={item.itemName} />
      </ListItem>
    ));
  };

  useEffect(() => setSrcInput(src), [src]);
  useEffect(() => {
    const readHTML = async () => {
      if (src.length) {
        debugger;
        const { data } = await axios.get(`https://jordans-cors-anywhere.herokuapp.com/${src}`);
        const imgHTML = data.match(/<meta.*?og:image.*?content="(.*?)"/im) || [
          "No Image",
          "data:image/svg+xml;base64,",
        ];
        const titleHTML = data.match(/<title.*?>(.*?)</im);
        setImg(imgHTML[1]);
        setTitle(titleHTML[1]);
      }
    };
    readHTML();
  }, [src]);

  return (
    <>
      <Box className="url-box">
        <>
          <TextField
            id="urlBar"
            label="url"
            style={{ margin: 8 }}
            fullWidth
            margin="normal"
            InputLabelProps={{
              shrink: true,
            }}
            variant="outlined"
            value={srcInput}
            onChange={(e) => setSrcInput(e.target.value)}
          />
          <IconButton aria-label="search" onClick={() => setSrc(srcInput)}>
            <i class="fas fa-search"></i>
          </IconButton>
        </>
        )
      </Box>
      <Box>
        <img src={img} alt="" style={{ height: "200px" }} />
        <div>{title}</div>
      </Box>
    </>
  );
};

export default ListView;
